import {Button, Dialog} from 'vue-weui';

export default {
  data() {
    return {
      dialog1Show: false,
      dialog2Show: false
    }
  },

  methods: {
    showDialog(id) {
      this[`dialog${id}Show`] = true;
    },

    handleDialogAction(action, id) {
      if (action === '确定') {
        this[`dialog${id}Show`] = false;
      } else {
        alert('你点击了“不关闭”，所以对话框不会消失^^');
      }
    }
  },

  components: {
    'weui-button': Button,
    Dialog
  }
}


