export default {
  props: {
    /**
     * 双向绑定的数据，用于控制是否显示Actionsheet
     */
    show: {
      type: Boolean,
      required: true,
      twoWay: true
    },

    /**
     * 菜单键值对
     */
    menus: {
      type: Object,
      required: false,
      default: {}
    },

    /**
     * 操作项键值对
     */
    actions: {
      type: Object,
      required: false,
      default: {}
    }
  },

  methods: {
    dispatchEvent(event, message) {
      this.$dispatch(event, message);
      if (event === 'weui-action-click') {
        this.hideActionSheet();
      }
    },

    hideActionSheet() {
      this.show = false;
    }
  }
}


