import CellHeader from './cell-header.vue';
import CellBody from './cell-body.vue';
import CellFooter from './cell-footer.vue';
import CellSelect from './cell-select.vue';

export default {
  props: {
    /**
     * 选项数组
     */
    options: {
      type: Array,
      required: true
    },

    /**
     * 选中项数据绑定，会用于select的v-model
     * @type {Object}
     */
    selected: {
      type: null,
      required: true,
      twoWay: true
    },

    /**
     * 是否为前置选择框，具体请参见下方示例
     */
    before: {
      type: Boolean,
      required: false,
      default: false
    },

    /**
     * 是否为后置选择框，具体请参见下方示例
     */
    after: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  components: {
    CellHeader,
    CellBody,
    CellFooter,
    CellSelect
  }
}


