import {Button, Toast} from 'vue-weui';

export default {
  data() {
    return {
      toastShow: false,
      loadingToastShow: false
    };
  },

  methods: {
    showToast() {
      this.toastShow = true;
      setTimeout(() => {
        this.toastShow = false;
      }, 2000);
    },
    showLoadingToast() {
      this.loadingToastShow = true;
      setTimeout(() => {
        this.loadingToastShow = false;
      }, 3000);
    },
  },

  components: {
    'weui-button': Button,
    Toast
  },
}


